import * as React from 'react';
import { IWPFromResponse } from '../model/IWPFromResponse';

export function Form() {
    // TODO: To Config
    return (
        <>
            <form
                action="https://www.pavolhejny.com/wp-json/contact-form-7/v1/contact-forms/2466/feedback"
                method="post"
                target="_blank"
                onSubmit={async (event) => {
                    event.preventDefault();
                    const form = event.target as HTMLFormElement;
                    const formData = new FormData(form);

                    if (!formData.get('gdpr')) {
                        alert(`Pot≈ôebujeme od V√°s za≈°krtnout souhlas se zpracov√°n√≠m osobn√≠ch √∫daj≈Ø.`);
                        return;
                    }

                    try {
                        const result = (await (
                            await fetch(form.action, {
                                method: 'POST',
                                body: formData,
                            })
                        ).json()) as IWPFromResponse;

                        if (result.status !== 'mail_sent') {
                            throw new Error(result.message);
                        }

                        form.reset();
                        alert(`Dƒõkujeme, m≈Ø≈æete se tƒõ≈°it na dal≈°√≠ email!`);
                    } catch (error) {
                        alert(error.message /*TODO: Better*/);
                    }
                }}
            >
                <input type="hidden" name="source" value={window.location.toString()} />

                <div className="group">
                    <input type="text" name="fullname" className="field" />
                    <label htmlFor="name">Va≈°e jm√©no:</label>
                    <div className="bar"></div>
                </div>

                <div className="group">
                    <input type="email" name="email" required defaultValue="@" className="field" />
                    <label htmlFor="email">E-mail: *</label>
                    <div className="bar"></div>
                </div>

                <div className="group">
                    <label>
                        <input type="checkbox" name="gdpr" />
                        Souhlas√≠m se zpracov√°n√≠m osobn√≠ch √∫daj≈Ø
                    </label>
                </div>

                {/* TODO: We need here some GDPR */}
                <div className="center">
                    <input value="P≈ôihl√°sit se " type="submit" id="submit" name="submit" className="button" />
                </div>
            </form>
        </>
    );
}
